
@font-face {
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 400;
  src: local('DM Sans Regular'), local('DMSans-Regular'),
    url('../fonts/dmsans-regular-webfont.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/dmsans-regular-webfont.woff') format('woff'), /* Modern Browsers */
       url('../fonts/DMSans-Regular.ttf') format('truetype'), /* Safari, Android, iOS */
}

@font-face {
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 500;
  src: local('DM Sans Medium'), local('DMSans-Medium'),
        url('../fonts/dmsans-medium-webfont.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/dmsans-medium-webfont.woff') format('woff'), /* Modern Browsers */
       url('../fonts/DMSans-Medium.ttf') format('truetype'), /* Safari, Android, iOS */
}

@font-face {
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 700;
  src: local('DM Sans Bold'), local('DMSans-Bold'),
        url('../fonts/dmsans-bold-webfont.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/dmsans-bold-webfont.woff') format('woff'), /* Modern Browsers */
       url('../fonts/DMSans-Bold.ttf') format('truetype'), /* Safari, Android, iOS */
}
@font-face {
  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  src: local('Titan One'), local('TitanOne'),
       url('../fonts/titanone-regular-webfont.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/titanone-regular-webfont.woff') format('woff'), /* Modern Browsers */
       url('../fonts/TitanOne-Regular.ttf') format('truetype'), /* Safari, Android, iOS */
    
}
