$primaryWhiteColor: #FFFFFF;
$bgColor: #FFF5F6;
$bgButtonColor: #ECEBEB;

$primaryTextColor: #907E82;
$secondaryTextColor: #000000;
$accentColor: #D41443;

$titleColor: #D41443;
$secondaryTitleColor: #FFA5BA;

$cubicParameters: cubic-bezier(0.4, 0, 0.2, 1);

img {
    display: block;
    width: inherit;
    height: inherit;
  }

//   .container {
//       margin: inherit;
//       padding: inherit;
//       width: inherit;
//       height: inherit;
//   }

.cow {
    // max-width: 1280px;
    padding: 125px 0 0 0;  
    margin: 0;   

    color: $primaryTextColor;
    font-family: 'DM Sans';
    font-size: 14px;
    line-height: 1.76;
    letter-spacing: 0.04em; 

    overflow-x:hidden;    
    
    /* background-color: lightblue; */
}

.cow--background {
    background-color: $bgColor;
}

.cow__title {
    margin: 0 20px 55px 20px;        
    
    text-align: center;    

    color: $titleColor;
    font-family: 'Titan One';
    font-weight: 400;
    font-size: 34px;
    line-height: 1.15;        
    text-transform: uppercase;   

    @media screen and (min-width: 320px) {
        margin: 0 60px 26px 60px;         
    }
  
    @media screen and (min-width: 772px) {
        margin: 0 171px 50px 171px;
        font-size: 48px;           
    }
  
    @media screen and (min-width: 1280px) {
        margin: 0 auto 55px auto;
        font-size: 58px;
        line-height: 1.14;
        letter-spacing: 0.06em;        
    }
}

/* cow-conteiner */
.cow-conteiner { 
    width: 100%;    
    padding: 0;
    margin: 0 auto;        
    
    // background-color: cyan;    

    @media screen and (min-width: 320px) {
        width: 320px;  
        // height: 1382px;       
    }

    @media screen and (min-width: 772px) {
        width: 772px;
        // height: 1254px;     
    }

    @media screen and (min-width: 1280px) {
        width: 1280px;  
        // height: 1688px;  
    }
}

.cow-conteiner__title {
    margin-top: 0;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 15px;
    
    text-align: center;

    color: $secondaryTitleColor;
    font-family: 'Titan One';
    font-weight: 400;
    font-size: 18px;
    line-height: 1.15;             
    text-transform: uppercase; 

    @media screen and (min-width: 772px) {
        font-size: 26px;                   
    }
  
    @media screen and (min-width: 1280px) {
        font-size: 30px;
        line-height: 1.14;
        letter-spacing: 0.06em;        
    }
}

.about {
    display: block;    
    // margin: 0;
    padding: 0;    
  
    @media screen and (min-width: 772px) {
        display: flex;   
        flex-wrap: nowrap;     
        padding: 0 36px 0 0;     
    }  

    @media screen and (min-width: 1280px) {   
        padding: 0 117px 0 0;
        
        background-image: url(../images/milk.png);        
        background-size: contain;
        background-position: bottom center;
        background-repeat: no-repeat;
    }
}

.cow-conteiner__img {
    display: block;
    width: 100%;      
    margin: 0 auto 46px auto;        

    @media screen and (max-width: 320px) {
        max-width: 280px;
    }

    @media screen and (min-width: 320px) {          
        margin: 0 20px 46px 20px;                 
        width: 280px;  
        height: 279px;
    }  

    @media screen and (min-width: 772px) {
        margin: 0 17px 0 39px;
        width: 340px;    
        height: 339px;           
    }
  
    @media screen and (min-width: 1280px) {
        margin: 0 74px 39px 112px;
        width: 516px;  
        height: 516px;         
    }
}

.cow-conteiner__description {
    display: block;       
    margin: 0 50px 40px 50px;
   
    font-family: 'DM Sans';       
    font-weight: 500;
    font-size: 12px;
    line-height: 1.62;
    letter-spacing: 0.00em;
    color: $secondaryTextColor;      

    @media screen and (min-width: 772px) {
        margin: 50px 36px 49px 0;   
        width: 340px;

        font-size: 14px;
        line-height: 1.76;
        // letter-spacing: 0.04em;
    }
  
    @media screen and (min-width: 1280px) {
        margin: 60px 117px 35px 0;
        width: 463px; 
        
        font-size: 16px;        
        letter-spacing: 0.04em;
    }
}

.cow-conteiner__information {  
    display: block;     
    // margin: 0 50px 40px 50px;   
    
    // background-color: lemonchiffon;
    
    @media screen and (max-width: 771px) {       
        margin: 0 0 40px 0; 
        background-image: url(../images/milk-320.png);
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
    }
    
    @media screen and (min-width: 772px) {
        margin: 0 36px 62px 0;   
        width: 340px;  
    }
  
    @media screen and (min-width: 1280px) {
        margin: 0 117px 45px 0;   
        width: inherit;   
    }
}

.cow-conteiner__text {    
    display: block;
    margin: 0 50px 17px 50px;

    font-family: DM Sans;       
    font-weight: 500;
    font-size: 12px;
    line-height: 1.62;
    letter-spacing: 0.04em;        
          
    @media screen and (min-width: 772px) {
        margin: 0 0 17px 0;
        font-size: 14px;
        line-height: 1.76; 
    }

    @media screen and (min-width: 1280px) {
        margin: 0 0 30px 0; 
        width: 463px;       
        line-height: 1.94;         
    }
}

.break {
    display: block;

    @media screen and (min-width: 772px) {
        display: inline;
    }
}

.cow-button {
    display: flex;
    padding: 0;
    margin: 0 auto 39px auto;
    width: 176px;
    height: 44px;

    border: 1px solid transparent;        
    border-radius: 22px;
    background-color: $primaryWhiteColor;

    color: $accentColor;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.29;        
    
    text-align: center;  
    align-items: center;   
    justify-content: center;
    text-decoration: none;

    cursor: pointer;
    transition: background-color 250ms $cubicParameters;
    
    @media screen and (min-width: 320px) {
        margin: 0 94px 39px 50px;    
    }

    @media screen and (min-width: 772px) {
        margin: 0 200px 66px 0;    
    }
    
    @media screen and (min-width: 1280px) {
        margin: 0 404px 146px 0;      
    }
}

.cow-button:hover,
.cow-button:focus {
    background-color: $bgButtonColor; 
} 

.cow-button__icon {
    padding: 0;
    margin-left: 13px;
    width: 6px;
    height: 10px;
    color: $accentColor;
    fill: transparent;                       
}